export default {
	data(){
		return {
			defaultData: {
				title: '规则提示',
				cancelText: '取消',
				sureText: '确定',
				textAlignSetting: 'left',
				fontSizeSetting: '30',
				cancelColor: '#999',
				sureColor: '#000',
			}
		}
	},
	props: {
		modelData: {
			type: Object
		}
	},
	methods: {
		cancelEvent: function() {
			this.$emit('cancelClick') 
		},
		sureEvent: function() {
			this.$emit('sureClick')
		}
	},
	mounted(){
		this.defaultData = {
			...this.defaultData,
			...this.modelData
		}
	}
}

